@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');
*{
  margin: 0;
  padding: 0;
}
.formContainer{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  align-self: center;
  padding: 32px;
  gap: 24px;
  width: 436px;
  border-radius: 20px;
  background: var(--blanco-fondo, #FEFEFE);
  filter: drop-shadow(1px 1px 9px #EDEDED);
}
.contentContainer{
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: flex-start;
  gap: 32px;
}
.contentContainer select {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 100%;
  height: 38px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 21px;
  letter-spacing: 0.02em;
  color: #707477;
}
.contentContainer select:focus {
  outline: none;
  border-radius: 10px;
  border: 2px solid var(--primary-400, #F5A973);
  background: var(--white-base, #FFF);
}
.InputConfigAA{
  padding: 0px 8px;
  height: 38px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  align-self: stretch;
  width: 516px;
}
.InputConfigAA::placeholder{
  color: var(--sistem-texto-placeholder, #6B7280);
font-family: Inter;
font-size: 12px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 0.28px;
}
.textCargaTuArchivo{
  color: var(--gray-600, #475467);
font-family: Inter;
font-size: 12px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 0.24px;
}
.InputConfig{
  padding: 0px 8px;
  height: 36px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  align-self: stretch;
  width: auto;
}
.toggleText{
  color: var(--gray-600, #475467);
font-family: Inter;
font-size: 12px;
font-style: normal;
font-weight: 400;
