.popUpMensajeExito{
    display: flex;
    align-items: center;
    align-self: flex-end;
    width: 360px;
    padding: 24px;
    gap: 24px;
    border-radius: 10px;
    background: var(--blanco-fondo, #FEFEFE);
    box-shadow: 8px 8px 12px 0px rgba(168, 168, 168, 0.10);
}
.popUpMensajeExitoContenido{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 8px;
    flex: 1 0 0;
}
.popUpMensajeExitoContenidoTitulo{
    color: var(--sistem-texto-negro-ttulo, #0C0C0F);
    font-family:"Inter";
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 0.32px;
}
.popUpMensajeExitoContenidoTexto{
    color: var(--sistem-texto-negro-ttulo, #0C0C0F);
    text-align: center;
    font-family:"Inter";
    font-size: 12px;
    font-style: normal;
    font-weight: 300;
    line-height: 19.5px; 
    letter-spacing: 0.26px;
}
.horizontal-bar {
    width: 304px;
    height: 8px;
    border-radius: 20px;
    background: var(--sistem-success-400, #32D583);
}
