@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

.cardBuyerPersona{
  display: flex;
  width: 412px;
  height: 90px;
  padding: 18px 24px;
  align-items: center;
  gap: 16px;
  flex-shrink: 0;
  background: var(--blanco-background, #FEFEFE);
  filter: drop-shadow(1px 1px 9px #EDEDED);
}
.cardBPIcon{
  display: flex;
  width: 70px;
  height: 70px;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  border-radius: 10px;
  background: var(--primary-200, #FBDAC3);
}
.cardBPContainer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 6px;
  padding: 0px;
  width: 100%;
  height: 80px;
}
.cardBPText{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 6px;
  width: 100%;
}
.cardBPTitle,
.cardBPClient,
.cardBPValue,
.cardBPValueTotal{
  font-family: 'Inter';
  font-style: normal;
  line-height: normal;
  
}
.cardBPTitle{
  color: var(--gray-600, #475467);
font-family: Inter;
font-size: 14px;
font-style: normal;
font-weight: 600;
line-height: normal;
letter-spacing: 0.28px;
}
.cardBPClient{
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.26px;
}
.cardBPValue{
  color: var(--gray-600);
  font-family: Inter;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.26px;
}
.cardBPValueTotal{
  color:var(--blue-700);
font-family: Inter;
font-size: 24px;
font-style: normal;
font-weight: 600;
line-height: normal;
letter-spacing: 0.48px;
}
.cardBPProgressBar{
  width: 100%;
  height: 8px;
  border-radius: 20px;
  background: var(--gray-100);
}
.cardBPProgressBarActive{
  height: 8px;
  border-radius: 20px;
  background: var(--primary-400);
}

@media  (max-width:1124px) {
  .cardBuyerPersona{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 8px 16px;
    gap: 6px;
    flex: 1 0;
    width: 90%;
  }  
  .cardBPTexto{
    color: var(--sistem-texto-negro-prrafo, #34343F);
    font-family:"Inter";
    font-size: 11px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.22px;
  }
  .cardBuyerPersonaValor p{
    color: var(--sistem-texto-negro-ttulo, #0C0C0F);
    font-family:"Inter";
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: 0.4px;
  }
}




