@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
  margin: 0;
  padding: 0;
}
.amplify_body_container{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.headerAmplify{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 0;
  width: 50%;
  height: 10vh; 
}
.mainAmplify{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  align-self: center;
}
.tituloPrincipalAmplify{
  display: flex;
  flex-direction: row;
  align-self: flex-start;

  color: #0C0C0F;
  font-family: 'Inter';
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.36px;
}

@media (min-width:1440px){
  .headerAmplify{
    align-self: center;
  }
  .mainAmplify{
    align-self: center;
  }
} 

/* Generales */

.borde-radius-gris{
  border-radius: 20px;
  border: 2px solid var(--gray-300, #D0D5DD);
  background: var(--white-base, #FFF);
}
.borde-inputs-generales{
  border-radius: 10px;
border: 2px solid var(--gray-300, #D0D5DD);
background: var(--white-background, #FEFEFE);
padding: 0px 8px;
}

.strong{
  color: var(--gray-600, #475467);
font-family: Inter;
font-size: 12px;
font-style: normal;
font-weight: 600;
line-height: 19.5px;
letter-spacing: 0.24px;
}
