@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

.cardSliderBarGeneroContainer{
  display: flex;
  width: 359px;
  padding: 24px;
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  border-radius: 10px;
  border: 1.5px solid var(--gray-borde, #E2E4E3);
  background: var(--blanco-fondo, #FEFEFE);
}
.cardSliderBarGeneroTitle,
.cardSliderBarGeneroTexto {
  font-family: 'Inter';
  
  font-style: normal;
  line-height: 19.5px; 
  letter-spacing: 0.26px;
}
.cardSliderBarGeneroTitle{
  color: var(--gray-600,#475467);
    font-weight: 600;

  font-size: 16px;
}
.cardSliderBarGeneroTexto{
  color: var(--sistem-texto-prrafo, #34343F);
  font-size: 12px;
  font-weight: 400;
 }
