@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');
*{
  margin: 0;
  padding: 0;
}
.home{
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  background: #F5F8FB;
}
.home_body_container {
  transition: margin-left 0.5s ease;
  height: 100vh;
  align-self: center;
}
.home_body_container.navbar-open {
  margin-left: 140px;
}
.headerHome{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 960px;
  height: 10%;
  gap: 10px;
}
.headerHome h1{
  color: #0C0C0F;
  font-family: 'Inter';
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.64px;
}
.headerHome span{
  color: #0C0C0F;
  font-family: 'Inter';
  font-size: 32px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 0.64px;
}
.mainHome{
  display: flex;
  width: 960px;
  padding: 24px 0px;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  flex: 1 0 0;
}
.homeBodyCardsState{
  display: flex;
  width: 100%;
  align-items: flex-start;
  gap: 32px;
  justify-content: space-between;
}
.tableContainerProcessExecuted{
  display: flex;
  flex-direction: column;
}


.loader1Home {
  height: 80px;
  width: 80px;
  border-radius: 80px;
  border: 3px solid var(--primary-default);
  top: 28%;
  top: -webkit-calc(50% - 43px);
  top: calc(50% - 43px);
  left: 35%;
  left: -webkit-calc(50% - 43px);
  left: calc(50% - 43px);
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  -webkit-animation: loader1 3s linear infinite;
  animation: loader1 3s linear infinite;
}
.loader1Home:after {
  content: "";
  position: absolute;
  top: -5px;
  left: 20px;
  width: 11px;
  height: 11px;
  border-radius: 10px;
  background-color: var(--primary-default);
}

@-webkit-keyframes loader1 {
    0% {
      -webkit-transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
    }
}
@keyframes loader1 {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
}

@media (max-width:1439px){
  .home_body_container.navbar-open {
    width: 960px;
  }
  .tableContainerProcessExecuted{
    display: flex;
    flex-direction: column;
    align-self: center;
    align-items: center;
    width: 100%;
  }
 
}

@media (min-width:1920px){
  .home_body_container.navbar-open{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    flex: 1 0 0;
    align-self: stretch;
  }
  .headerHome{
    align-self: center;
  }
  .mainHome{
    align-self: center;
  }
} 