@font-face {
  font-family: 'I';
  src: url('https://fonts.googleapis.com/css2?family=Inter:wght@300&display=swap');
}

.Configuracion{
  display: flex;
  width: 433px;
  padding: 32px;
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  background: #FEFEFE;
  align-self: center;
  filter: drop-shadow(1px 1px 9px #EDEDED);

}
.Configuracion_FormContainer h1{
  margin-bottom: 16px;
} 
.Configuracion_FormContainer{
  display: flex;
  flex-direction: column;
  gap: 16px;
  width: 100%;
}
.Configuracion_FormContainer p {
  color: var(--sistem-texto-negro-ttulo, #0C0C0F);
  font-family: 'Inter';
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  padding: 0px 8px;
}
.formConfig {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  gap: 4px;
  margin: 16px 0;
}
.SelectConfig{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  height: 38px;
  width: 100%;
  color: var(--sistem-texto-negro-placeholder, #707477);
  font-family: 'Inter';
  font-size: 12px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  letter-spacing: 0.26px;
}
.SelectConfig:focus{
  outline: none;
  border-radius: 10px;
  border: 2px solid var(--primary-400, #F5A973);
  background: var(--white-base, #FFF);
}
.selectConfigText{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 4px;
  align-self: stretch;
}
.Configuracion_botones {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  align-self: flex-end;
  padding: 0px;
  gap: 16px;
}
.secondaryButton {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 8px 12px;
  width: 122px;
  height: 40px;
  background: #FEFEFE;
  color: #707477;
  border: 1.5px solid #E2E4E3;
  border-radius: 20px;
  cursor: pointer;
}
.primaryButton{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 8px 12px;
  gap: 10px;  
  width: 122px;
  height: 40px;
  background: #FF8C00;
  color: #FEFEFE;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  cursor: pointer;
}

@media (max-width:1439px){
  .Configuracion{
    display: flex;
    width: 301px;
    padding: 24px;
    flex-direction: column;
    align-items: flex-start;
    gap: 40px;
  }
  .SelectConfig{
    display: flex;
    height: 32px;
    padding: 0px 8px;
  }
  .btnPrimaryBuyerPersona{
    display: flex;
height: 38px;
padding: 8px 12px;
flex-direction: column;
justify-content: center;
align-items: center;
gap: 10px;
width: 120px;
  }
  .btnSecondary{
width: 144px;
  }
  .Configuracion_botones{
    gap: 12px;
  }
}