@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
  margin: 0;
  padding: 0;
}

.cardEnriquecerDatos {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 24px 32px;
  gap: 24px;
  width: 499px;
  height: 203px;
  background: #FEFEFE;
  box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.10);;
  border-radius: 20px;
  background-image: url(./buyerpersonCardBack.svg);
  background-position: right 0px top -30px;
  background-repeat: no-repeat;
  background-size: cover;
}

.cardEnriquecerDatosTextoTitle {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 27px;
  display: flex;
  align-items: center;
  letter-spacing: 0.02em;
  color: #0C0C0F; 
}

.cardEnriquecerDatosTexto {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 21px;
  display: flex;
  align-items: center;
  letter-spacing: 0.02em;
  color: #4B4B5A;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 0px;
  gap: 10px;
  width: 499.5px;
  height: 100px;
}

.cardEnriquecerDatosButtons {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 0px;
  gap: 7px;
  width: 195px;
  height: 28px;
}

.cardEnriquecerDatosButtonsPrimary {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 2px 12px;
  gap: 4px;
  width: 54px;
  height: 28px;
  background: #FF8C00;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 10px;
  line-height: 24px;
  display: flex;
  align-items: center;
  letter-spacing: 0.02em;
  color: #FEFEFE;
}

.cardEnriquecerDatosButtonsSecondary {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2px 12px;
  gap: 10px;
  width: 112px;
  height: 28px;
  background: #FEFEFE;
  border: 1px solid #E2E4E3;
  border-radius: 8px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 10px;
  line-height: 24px;
  display: flex;
  align-items: center;
  letter-spacing: 0.02em;
  color: #4B4B5A;
}

.popupOverlay{
    display: flex;
    position: absolute;
    margin-left: 178px;
    margin-top: 50px;
  }