* {
    margin: 0;
    padding: 0;
}

.inputGeneral{
    display: flex;
    width: -webkit-fill-available;
height: 36px;
    align-items: center;
    flex-shrink: 0;
    color: var(--gray-600, #475467);
    font-family: 'Inter';
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 19.5px; /* 139.286% */
    letter-spacing: 0.28px;
}
.inputGeneral::placeholder{
    color: var(--gray-400, #98A2B3);
    font-family: 'Inter';
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.28px;
}
