

.popUpBP {
  display: flex;
  width: 130px;

  padding: 16px;
  flex-direction: column;
  align-items: flex-start;
  gap: 6px;
  border-radius: 8px;
  background: #FEFEFE;
  box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.10);
}
.popUpBPContent, .popUpBPContentDisabled{
  display: flex;
  height: 32px;
  align-items: center;
  gap: 3px;
  align-self: stretch;
  cursor: pointer;
}
.popUpBPContent span{
  color: var(--gray-600, #475467);
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 0.24px;
}
.popUpBPContentDisabled span{
  color: var(--gray-400, #98A2B3);
font-family: Inter;
font-size: 12px;
font-style: normal;
font-weight: 500;
line-height: normal;
letter-spacing: 0.24px;
}
.PopUpVisualizar{
  width: 24px;
  height: 24px;
}
.PopUpDescargar{
width: 24px;
height: 24px;
}
.PopUpTrash{
  width: 24px;
height: 24px;
}