.cardDatosEconomicos{
    display: flex;
    width: 359px;
    padding: 24px;
    flex-direction: column;
    align-items: flex-start;
    gap: 24px;
    border-radius: 10px;
    border: 1.5px solid var(--gray-borde, #E2E4E3);
    background: var(--blanco-fondo, #FEFEFE);
}
.cardDatosEconomicosTitulo,
.cardDatosEconomicosTexto{
    font-family: 'Inter';

    font-style: normal;
    line-height: 19.5px; 
    letter-spacing: 0.26px;
}
.cardDatosEconomicosTitulo{
    color: var(--gray-600,#475467);
    font-weight: 600;
    font-size: 16px;
}
.cardDatosEconomicosTexto{
    color: var(--sistem-texto-prrafo, #34343F);
font-family: Inter;
font-size: 12px;
font-weight: 400;
}
.cardDatosEconomicosSelector{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
    align-self: stretch;
}
.cardDatosEconomicosSelector p,
.cardDatosEconomicosSelector select{
    font-family: 'Inter';
    font-style: normal;
    line-height: normal;
}
.cardDatosEconomicosSelector p{
    color: var(--sistem-texto-negro-ttulo, #0C0C0F);
    font-size: 12px;
    font-weight: 400;
}
.cardDatosEconomicosSelector select{
    display: flex;
    height: 38px;
    padding: 0px 8px;
    justify-content: center;
    gap: 10px;
    align-self: stretch;

    color: var(--sistem-texto-negro-prrafo, #707477);
    font-size: 12px;
    font-weight: 300;
    letter-spacing: 0.26px;
}
.cardDatosEconomicosSelector select:focus {
    outline: none;
    border-radius: 10px;
border: 2px solid var(--primary-400, #F5A973);
background: var(--white-base, #FFF);
  }

  @media (max-width:1439px) {
    .cardDatosEconomicos{
        display: flex;
        width: 359px;
        padding: 24px;
        flex-direction: column;
        align-items: flex-start;
        gap: 24px;
    }
  }