@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
}

.bodyBox {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: 6.8em;
    padding: 16px;
    gap: 12px;
    border-radius: 20px;
    border: 2px solid var(--gray-300, #D0D5DD);
    background: var(--base, #FEFEFE);
    position: absolute;
    z-index: 3;
    bottom: 50px;
}

.bodyBox.closed {
    left: 84px;
    width: 12em;
}

.bodyBox.expanded {
    left: 221px;
}

.bodyBoxUP,
.bodyBoxDown {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-self: stretch;
}

.bodyBoxUP {
    gap: 6px;
}

.bodyBoxDown {
    gap: 16px;
}

.bodyBoxDown {
    padding: 8px;
    border-radius: 8px;
}

.bodyBoxPerfil {
    display: flex;
    align-items: center;
    gap: 6px;
    align-self: stretch;
}

.bodyBoxImg {
    display: flex;
    width: 40px;
    height: 40px;
    justify-content: center;
    align-items: center;
    border-radius: 4px;

}

.bodyBoxImgSecond {
    margin-left: 4px;
}

.bodyBoxPerfil img,
.bodyBoxCompany img {
    width: 24px;
    height: 24px;
}



.bodyBoxCompany {
    display: flex;
    align-items: center;
    align-self: stretch;
    /* padding: 0px 8px 0px 4px; */
    gap: 6px;
}

.dividerBox {
    border: 0.5px solid #E2E4E3;
    width: 100%;
}

.bodyBoxCountryContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 4px;
    align-self: stretch;
    /* margin-bottom: 16px; */
}

.bodyBoxCountryContainer p {
    color: var(--sistem-texto-negro-prrafo, #707477);
    font-family: 'Inter';
    font-size: 11px;
    font-style: normal;
    font-weight: 500;
    line-height: 15px;
    /* 136.364% */
    letter-spacing: 0.22px;
}

.bodyBoxCountry {
    display: flex;
    height: 32px;
    padding: 4px 8px;
    justify-content: space-between;
    align-items: center;
    align-self: stretch;
    border-radius: 8px;
    border: 1px solid var(--gray-borde, #E2E4E3);
    background: var(--blanco-base, #FFF);
}

.custom-option {
    display: flex;
    padding: 8px;
    gap: 8px;
    border-radius: 8px;
    background: var(--blanco-base, #FFF);
}

.bodyBoxTextItem {
    width: 100%;
    padding: 4px 0px;
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.bodyBoxLeftItem {
    display: flex;
    align-self: stretch;
    justify-content: space-between;
    align-items: center;

    gap: 4px;
    margin-bottom: 16px;
}

.bodyBoxRightItem {
    display: flex;
    justify-content: flex-end;
    width: 100%;
    gap: 4px;
    flex-direction: column;

}

.bodyBoxBuyerPersona {
    display: flex;
    align-items: flex-start;
    gap: 4px;
}

.bodyBoxBuyerPersona img {
    height: 28px;
    width: 28px;

}

.bodyBoxBuyerPersona p {
    align-self: center;
    color: var(--gray-600, #475467);
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 0.24px;
    width: max-content;
}

.bodyBoxItemText {
    color: var(--gray-600, #475467);
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 0.32px;
}

.bodyBoxItemTextSecond {
    color: var(--gray-600, #475467);
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    letter-spacing: 0.26px;
}

.bodyBoxValue {
    color: var(--gray-800, #1D2939);
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: 0.26px;
}

