@media (max-width:1439px) {

    .bodyBox.closed {
        left: 68px;
  
    }

    .bodyBox.expanded {
        left: 164px;
    }
}

@media (min-width: 1920px) {
  
    .bodyBox.expanded {
        left: 221px;
    }
}



