.imgBuyerPersona {
    border-radius: 6px;

    display: flex;
    width: 52px;
    height: 52px;
    justify-content: center;
    align-items: center;


}

.imgAmplifyPersona {
    border-radius: 6px;

    display: flex;
    width: 52px;
