.bodyBoxLeftItem {
    display: flex;
    align-items: center;
    gap: 6px;
    align-self: stretch;

    -webkit-transition: all .5s ease;
    -moz-transition: all .5s ease;
    transition: all .5s ease;
}

.bodyBoxLeftItemNone {
    overflow: hidden;
    height: 0;
    gap: 0px;
    margin-bottom: 0em;
    width: 100px;
    -webkit-transition: all .5s ease;
    -moz-transition: all .5s ease;
    transition: all .5s ease;
}

.css-1n6sfyn-MenuList {
    height: 10em;
}

