@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

.enterpriceposition{
  display: flex;
  justify-content: space-between;
}
/* Boton para expandir */
.boxBrandButton{
  display: flex;
}
.toggleButton, 
.toggleButtonOpen {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 10px;
  gap: 10px;
  position: absolute;
  top: 14px;
  border-radius: 50px;
  background: #FEFEFE;
  box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.10);
  cursor: pointer;
}
.toggleButton{
  left: 55px;
}
.toggleButtonOpen{
  left: 194px;
}
.toggleButton img,
.toggleButtonOpen img{
  width: 24px;
  height: 24px;
}
/* Navbar */
.navbar {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 64px; /* ancho total de 80px - 16px de padding */
  height: -webkit-fill-available;
  gap: 80px;
  padding: 24px 8px;
  border-right: 2px solid var(--gray-300, #D0D5DD);
  background: #FEFEFE;
  z-index: 999;
}
.navbarLink {
  display: flex;
}
.navbarLink span{
  color: var(--gray-600, #475467);
  font-family: 'Inter';
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: -0.14px;
}
.IconContainerNav{
  width: 24px;
  height: 24px;
}
.selected  {
  background: #F2F4F7;
  border-radius: 8px;
}
.active{  background: #F2F4F7; }
/* Navbar cerrado */
.menuPrincipal{
  height: 100%;
}
.menuPrincipal,
.menuSecundario {
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 100%;
  align-items: center;
}

.menuPrincipal a,
.menuSecundario a,
.menuSecundario button{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
}
.menuPrincipal a:hover,
.menuSecundario a:hover,
.menuSecundario button:hover {
  background: #EFF8FF;
  border-radius: 8px;
}

/* Navbar abierto */
.navbar.expanded {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 185px; /* ancho total de 217px - 16px de padding */
  height: -webkit-fill-available;/* largo total de 900px - 24px de padding */
  padding: 24px 16px;
  gap: 80px;
  border-right: 2px solid var(--gray-300, #D0D5DD);
  background: #FEFEFE;
}
.boxBrandNav {
  display: flex;
  align-self: center;
}
.boxBrandNav img{
  width: 106.667px;
  height: 24px;
}
.menuContentContainer {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;
  height: 100%;
}
.menuPrincipalAbierto{
  /* height: 500px; */
}
.menuPrincipalAbierto,
.menuSecundarioAbierto {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  gap: 8px;
}
.menuPrincipalAbierto{
  height: 100%;
}

.divider{
  border: 1px solid #E2E4E3;
  width: 100%;
}
.menuPrincipalAbierto a,
.menuSecundarioAbierto a,
.menuSecundarioAbierto button{
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 161px;
  height: 24px;
  padding: 12px;
  gap: 6px;
  border-radius: 8px;
}
.menuPrincipalAbierto a:hover,
.menuSecundarioAbierto a:hover {
  background: #EFF8FF;
  border-radius: 8px;
}
 .ImagenSeparacionNavbar{
  display: none;
  }
.navbarLink:hover .ImagenSeparacionNavbar{
display: block;
}
.menuSecundarioAbierto button{
  display: flex;
  align-items: center;
  align-self: stretch;
  flex-direction: row;
  height: 48px;
  padding: 12px;
  gap: 4px;
  background: #FEFEFE;
  border-radius: 8px;
  width: 100%;
}


/*  768 */
@media (max-width: 1439px) {
  .toggleButton{
    display: none;
  }
  .boxBrandNav img{
    width: 80px;
    height: 18px;
  }
  .navbar{
    width: 36px; 
    height: -webkit-fill-available;
    padding: 24px 14px;
    border-radius: 2px;
  }
  .navbar.expanded{
    width: 144px;
    height: -webkit-fill-available;
    padding: 24px 8px;
  }
  .navbarLink img{
    display: flex;
    width: 20px;
    height: 20px;
  }
  .navbarLink span{
    font-size: 12px;
    letter-spacing: -0.12px;
  }
 
  .menuPrincipalAbierto{
    /* height: 410px; */
  }
  .menuPrincipal a,
  .menuSecundario a{
    width: 12px;
    height: 12px;
    padding: 12px;
    gap: 10px;
  }
  .menuSecundario button{
    width: 36px;
    height: 36px;
    padding: 12px;
    gap: 10px;
  }
  .toggleButtonOpen img,
  .toggleButton img{
    width: 16px;
    height: 16px;
  }
  .toggleButton {
    left: 45px;
    top: 15px;
  }
  .toggleButtonOpen{
    left: 140px;
    top: 16px;
  }
  .menuPrincipalAbierto a,
  .menuSecundarioAbierto a{
    display: flex;
    height: 36px;
    padding: 0px 12px;
    align-items: center;
    gap: 6px;
    align-self: stretch;
    width: 120px;
  }
  .menuSecundarioAbierto button{
    display: flex;
    align-items: center;
    align-self: stretch;
    flex-direction: row;
    height: 36px;
    padding: 0px 12px;
    gap: 2px;
    background: #FEFEFE;
    border-radius: 8px;
    width: 100%;
  }
}
.separacinTextoImagen{
  width: 100%;
  display: flex;
  justify-content:space-between ;
}
.ImagenSeparacionNavbar{

}
/* desde 1920 */
@media (min-width: 1920px){
  .boxBrandNav img{
    width: 124.444px;
    height: 28px;
  }
  .toggleButtonOpen {
    display: none;
  }
  .navbar.expanded{
    padding: 24px 16px;
  }
  .menuPrincipalAbierto{
    /* height: 676px; */
  }
}




