@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

a {
  text-decoration: none;
}
/* Badget */
.procesandoDatos,
.modelandoDatos,
.analizandoPerfiles,
.badgedConExito,
.badgedSinExito {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 6.2px 4.2px;
  height: 8px;
  border-radius: 6px;

  color: var(--gray-600, #475467);
  font-family: 'Inter';
  font-size: 10px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 0.05px;
}
.procesandoDatos,
.modelandoDatos,
.analizandoPerfiles{
  width: 111.6px;
}
.badgedConExito,
.badgedSinExito{
  width: 46px;
}
.procesandoDatos {
  border: 0.25px solid var(--gray-300, #D0D5DD);
  background: var(--gray-50, #F9FAFB);
}
.modelandoDatos{
  border: 0.25px solid var(--gray-300, #D0D5DD);
  background: var(--gray-200, #E4E7EC);
}
.analizandoPerfiles{
  border: 0.25px solid var(--sistem-warnig-300, #FEC84B);
  background: var(--sistem-warnig-100, #FEF0C7);
}
.badgedConExito{
  border: 0.25px solid var(--sistem-success-300, #6CE9A6);
  background: var(--sistem-success-100, #D1FADF);
}
.badgedSinExito{
  border: 0.25px solid var(--sistem-error-300, #FDA29B);
  background: var(--sistem-error-100, #FEE4E2);
}

/* General */
.tablaHome{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  border-radius: 20px;
  max-height: 400px; 
  overflow-y: auto;
  border: 1.5px solid var(--gray-300, #D0D5DD);
}
.tablePrincipalContainer {
  display: flex;
  flex-direction: column;
  width: 960px;
  filter: drop-shadow(1px 1px 9px #EDEDED);

  
}
.tableContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  padding: 24px;
  width: 908px;
  border-radius: 0px 0px 20px 20px;
  border: 2px solid var(--gray-300, #D0D5DD);
  background: var(--blanco-base, #FFF);
  filter: drop-shadow(1px 1px 9px #EDEDED);
}
/* Header Contenedor*/
.tableHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 24px;
  background-color: #fff;
}
.tableHeader p {
  color: var(--gray-600, #475467);
  font-family: 'Inter';
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 0.32px;
}
.search-input {
  position: relative;
  display: flex;
  align-items: center;
}
.search-input input {
  display: flex;
  width: 300px;
  align-items: center;
  height: 36px;
  font-size: 12px;
}
.search-input input::placeholder{
  color: var(--gray-400, #98A2B3);
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.28px;
}
.inputSearchHome:focus-visible{
  border-radius: 10px;
  border: 2px solid var(--primary-400, #F5A973);
  background: var(--white-base, #FFF);
height: 34px;
}

.inputSearchHome::placeholder{
  padding-left: 24px;
}
.search-input img{
  position: absolute;
  margin-left: 9px;
}
.tableTabs {
  display: flex;
  align-items: center;
  align-self: stretch;
  /* gap: 4px; */
}
.tableTabs .tabsNoActive,
.tableTabs .tabsActive{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12px;
  gap: 8px;
  flex: 1 0 0;
  border-radius: 20px 20px 0px 0px;
  border-top: 2px solid var(--gray-300, #D0D5DD);
  border-right: 2px solid var(--gray-300, #D0D5DD);
  border-left: 2px solid var(--gray-300, #D0D5DD);

  font-family: 'Inter';
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}
.tableTabs .tabsNoActive {
  background: var(--gray-50);
  color: var(--gray-600, #475467);
}
.tableTabs button.tabsActive {
  background: var(--white-base);
  color: var(--primary-500, #F18F49);
}

/* TABLA: Header */
.tableContainer table {
  width: 100%;
  border-collapse: collapse;
  border-radius: 20px;
  table-layout: fixed;
}
.tableContainer thead {
  border-bottom: 1px solid var(--gray-300, #D0D5DD);
}
.tableContainer tr {
  border-bottom: 0.5px solid var(--gray-borde, #E2E4E3);
}
.tableContainer th{
  height: 46px;
  padding: 0px 8px;
  border-bottom: 0.5px solid var(--gray-300, #D0D5DD);
  background: var(--gray-50, #F9FAFB);
}
.thFilter {
  color: var(--gray-500, #6D7A8F);
  font-family: 'Inter';
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 0.24px;
}
.thRegistro, .thAction{
  width: 85px;
}
.thEstado, .thFecha {
  width: 130px;
}
.thProceso{
  width: 120px;
}
.thHeader{
  display: flex;
padding: 8px;
}
/* TABLA: Filtro */
.thHeader.thHovered {
  border-radius: 8px;
  border: 3px solid var(--secundario-blue-light-50, #F0F9FF);
}
.thInnerContainer:hover{
  border: 1px solid var(--secundario-blue-light-200, #B9E6FE);
}
.thInnerContainer {
  display: flex;
  padding: 8px 8px 8px 4px;
  justify-content: space-between;
  align-items: center;
  border-radius: 8px;
}
.thInnerContainer.thSorted{
  border: 1px solid var(--secundario-blue-light-500, #0BA5EC);
}
.thHeader.thSorted {
  border-radius: 8px;
  border: 3px solid var(--secundario-blue-light-100, #E0F2FE);
}

/* TABLA: Body */
.tableContainer td {
  height: 48px;
  padding: 0px 16px;
}
.tdName, .tdOrganization, 
.tdIndustry, .tdAmount, 
.tdCreated, .tdBadged, .verticalIcon{
  /* color: var(--sistem-texto-negro-texto, #707477); */
  color: var(--gray-500, #6D7A8F);
  font-family: 'Inter';
  font-size: 12px;
  font-style: normal;
  line-height: normal;
  letter-spacing: 0.24px;
  overflow: hidden;
    white-space: revert;
}
.tdName, .tdAmount{
  font-weight: 500;
}
.tdOrganization, .tdIndustry,
.tdCreated{
  font-weight: 400;
}
.verticalIcon{
  text-align: center;
}
/* Buyer Persona */ /* Amplify */
.tableBuyerPerson,
.tableAmplify{
  display: flex;
  flex-direction: column;
  width: 100%; 
}

@media screen and (max-width: 1024px) {
  .tablePrincipalContainer{
    width: 90%;
  }
  .tableContainer{
    width: 850px;
  }
  .thRegistro, .thAction{
    width: 40px;
  }
  .thEstado{
    width: 100px;
  } 
  .thFecha {
    width: 90px;
  }
}

@media  (max-width:1439px) {
  .tableContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 24px;
    gap: 24px;
    border-radius: 0px 0px 20px 20px;
    border: 2px solid var(--gray-300, #D0D5DD);
    background: var(--white-base, #FFF);
  }
  .tableTabs {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0px;
    /* gap: 4px; */
    align-self: flex-start;
    width: 100%;
  }
  .tablePrincipalContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .tabla {
    overflow-x: auto;
    display: block;
  }
  th,
  td {
    white-space: nowrap;
  }
  .tdName , .tdOrganization , .tdIndustry , .tdAmount , .tdCreated   {
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .filtro-options {
    margin-top: 5px;
    border: none;
  }
  .thFilter{
    color: var(--gray-500, #6D7A8F);
    font-family: 'Inter';
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 0.24px;
  }
  .tdIndustry{
    overflow: hidden;
    white-space: revert;
  }
  .tdName{
  overflow: hidden;
  white-space: revert;
  }
  
