@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');
a {
  text-decoration: none;
}
.automaticoTexts{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 16px;
  align-self: stretch;
}
.amplifyPrincipalContainer {
  display: flex;
  flex-direction: column;
  align-self: flex-start;
  

}
.amplifyTabs {
  display: flex;
  /* gap: 4px; */
}
.amplifyTabs button,
.amplifyTabs button.tabsActive {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12px;
  gap: 8px;
  flex: 1 0 0;
  border-radius: 20px 20px 0px 0px;
  border-top: 2px solid var(--gray-300, #D0D5DD);
  border-right: 2px solid var(--gray-300, #D0D5DD);
  border-left: 2px solid var(--gray-300, #D0D5DD);
  background: var(--gray-50, #F9FAFB);
  font-family: 'Inter';
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}
.amplifyTabs button {
  background: var(--gray-50, #F9FAFB);
  color: var(--gray-600, #475467);
}
.amplifyTabs button.tabsActive {
  background: var(--white-base);
  color: var( --primary-500);
}
.amplifyContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 24px 32px;
  gap: 24px;
  border-radius: 0px 0px 20px 20px;
  border: 2px solid var(--gray-300, #D0D5DD);
  background: var(--white-background, #FEFEFE);
  width: 1107px;
  filter: drop-shadow(1px 1px 9px #EDEDED);

}

.amplifyContainerExtend {
  width: 1232px;
  padding: 24px 32px;
}
.automaticoContainer{
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
}
.automaticoContainer h1,
.automaticoContainerText{
  color: var(--sistem-texto-negro-ttulo, #0C0C0F);
  font-family: 'Inter';
  font-style: normal;
}
.automaticoContainer h1{
  font-size: 16px;
  font-weight: 600;
  line-height: 24px; 
  letter-spacing: 0.32px;
}
.automaticoContainerText{
  color: var(--gray-600, #475467);
font-family: Inter;
font-size: 12px;
font-style: normal;
font-weight: 400;
line-height: 19.5px; /* 139.286% */
letter-spacing: 0.28px;
}
.solicitudBuyerPersonaAutomatico {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: 24px;
  align-self: stretch;
  width: 100%;
}
.solicitudBPAutomaticoExtend{
  width: 1232px;
}
.containerInputAmplify {
  display: flex;
  /* padding: 10px 6px; */
  flex-direction: column;
  justify-content: center;

  align-self: stretch;
  gap: 8px;
  width: 100%;
}
.containerInputAmplify select{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 100%;
  height: 38px;
  padding: 0px 8px;
  border-radius: 8px;
  border: 2px solid var(--gris-gris-borde, #E2E7F1);
  background: var(--blanco, #FFF);
  color: #707477;
  font-family: 'Inter';
  font-size: 12px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  letter-spacing: 0.26px;
}
.containerInputAmplify select:focus {
  border-radius: 10px;
  border: 2px solid var(--primary-400);
  background: var(--white-base);
}
.Confirmacion_botones{
  display: flex;
  flex-direction: row;
justify-content: flex-end;
align-items: center;
gap: 16px;
align-self: stretch;
 }
.containerInputAmplify option{
  border-radius: 8px;
  background: var(--blanco-base, #FFF);
  box-shadow: 6px 2px 20px 0px rgba(172, 172, 172, 0.25);
  color: var(--sistem-texto-negro-prrafo, #34343F);
  font-family: 'Inter';
  font-size: 12px;
  font-style: normal;
  font-weight: 300;
  line-height: 19.5px; /* 150% */
  letter-spacing: 0.26px;
}
.containerInputAmplify p{
  padding: 0px 6px;
  color: var(--negro-ttulos, #0C0C0F);
  font-size: 12px;
  font-family: 'Inter';
  font-weight: 400;
}
.contenedorBotonesAmplify{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 16px;
  align-self: stretch;
}
.botonAtrasAmplify{
  display: flex;
  width: 146px;
  padding: 8px 12px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  border: 0.5px solid var(--gris-borde, #E2E4E3);
  background: var(--blanco-fondo, #FEFEFE);
  color: var(--negro-texto, #4B4B5A);
  font-size: 14px;
  font-family:"Inter";
  font-weight: 500;
  line-height: 21px;
  letter-spacing: 0.28px;
}
.botonSeleccionarAmplify{
  display: flex;
  width: 146px;
  padding: 8px 12px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  background: #FFEFE0;
  color: #989898;
  font-size: 14px;
  font-family:"Inter";
  line-height: 21px;
  letter-spacing: 0.28px;
}
.buyerPersonaContainer{
  display: flex;
  padding: 24px;
  justify-content: center;
  align-items: flex-start;
  align-self: stretch;
  border-radius: 20px;
  background: var(--secundario-blue-ligth-50, #F0F9FF);
  display: flex;
  align-items: flex-start;
  gap: 24px;
  flex: 1 0 0;
  flex-direction: column;
}
.buyerPersonaCardContainer{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  width: 30%;
}
.buyerPersonaCardContent{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex: 1 0 0;
}
.buyerPersonaCardTextoPerfiles{
  display: flex;
  align-items: flex-start;
  gap: 24px;
  align-self: stretch;
}
.amplifyDesplegableContainer{
  display: flex;
  padding: 24px;
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  border-radius: 20px;
  border: 1.5px solid var(--gray-borde, #E2E4E3);
  background: var(--blanco-base, #FFF);
  width: 1060px;
  align-self: center;
}
.cardCantidadPerfiles{
  display: flex;
  padding: 24px;
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  border-radius: 12px;
  border: 1.5px solid var(--gray-borde, #E2E4E3);
  background: var(--blanco-fondo, #FEFEFE);
  width: 1012px;
}
.cardCantidadPerfilesHeader{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  align-self: stretch;
}

.cardCantidadPerfilesHeader p {
  font-family: 'Inter';
  font-size: 12px;
  font-style: normal;
  line-height: 19.5px; 
  letter-spacing: 0.26px;
}
.cardCantidadPerfilesHeader h2 {
  color: var(--gray-600, #475467);
  font-weight: 600;
  font-size: 16px;
}

.cardCantidadPerfilesHeader p{
  color: var(--sistem-texto-prrafo, #34343F);
  font-size: 12px;
  font-weight: 400;
  line-height: 19.5px; /* 139.286% */
  letter-spacing: 0.28px;
}
.inline-paragraph {
  display: inline; 
  
}
.TextSecondPerfiles{
  color: var(--sistem-texto-negro-prrafo, #34343F);
  font-weight: 400;
}
.cardContainerDatosEcoGen{
  display: flex;
  align-items: flex-start;
  align-self: stretch;
  gap: 24px;
}
.cardCantidadPerfilesBody,
.cardDatosEcoGeo {
  display: flex;
  align-items: flex-start;
  gap: 24px;
  align-self: stretch;

  width: 999px;
}
.cardDatosEcoGeo {
  flex-direction: column;
}
.cardContainerDatosGeo{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
}
.Confirmacion_botones{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 16px;
  align-self: stretch;
}

@media (max-width:1439px){
  
  .cardDatosEcoGeo {
    flex-direction: column;
  }
  .buyerPersonaCardContent{
    width: 100%;
  }
  
  .amplifyContainerExtend {
    width: 1080px;
  }
}

@media (min-width:1441px){
  .amplifyContainer{
    width: 1080px;
  }
  .amplifyContainerExtend {
    width: 1232px;
    padding: 24px 32px;
  }
  .solicitudBPAutomaticoExtend{
    width: 1232px;
  }
}
