.connectionErrorCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    gap: 44px;
    width: 100%;
    height: 100vh;
    border-radius: 8px;
    justify-content: center;
}
.connectionError_container{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;
    gap: 19px;
    width: 300px;
}
.connectionError_container h1 {
    color: var(--negro-ttulos, #0C0C0F);
    text-align: center;
    font-family:"Inter";
    font-size: 26px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 0.52px;
}
.textConnectionError {
    width: 100%;
    height: 100%;
    color: var(--textos-negro-textos, #4B4B5A);
    text-align: center;
    font-family: 'Inter';
    font-size: 12px;
    font-style: normal;
    font-weight: 300;
    line-height: 24px;
}

