.cardAcordeon {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  align-self: stretch;
  border-radius: 8px;
  background: var(--blanco-fondo, #FEFEFE);
}
.card-header {
  display: flex;
  align-items: center;
  align-self: stretch;
  justify-content: space-between;
  padding: 12px 16px;
  gap: 10px;
  border-radius: 8px;
}
.card-header h2 {
  color: var(--negro-texto, #4B4B5A);
  font-family: 'Inter';
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 19.5px; 
  letter-spacing: 0.26px;
}
.card-body {
  border-radius: 0px 0px 8px 8px;
  padding: 12px 16px;
}
.card-body p {
  color: var(--negro-texto, #4B4B5A);
  font-family: 'Inter';
  font-size: 12px;
  font-style: normal;
  font-weight: 300;
  line-height: 19.5px; 
  letter-spacing: 0.26px;
}