.ListEnterpricesBox{
    border-radius: 6px;
border: 1px solid var(--gray-300, #D0D5DD);
background: var(--base, #FEFEFE);
display: flex;
width: 190px;
padding: 8px;
flex-direction: column;
align-items: flex-start;
gap: 8px;
position: absolute;
bottom: 2px;
overflow-y: scroll;
overflow-x: hidden;
height: 280px;
}

.PopUpboxNavbarDataListEnterprices{
    border-radius: 6px;
border: 1px solid var(--gray-300, #D0D5DD);
background: var(--base, #FEFEFE);
display: flex;
width: max-content;
padding: 8px;
flex-direction: column;
align-items: flex-start;
gap: 8px;
position: absolute;
height: 1em;
top: 0; /* Ajusta la posicin verticalmente segn tus necesidades */
left: 100px; 

}
.ListEnterpricesBox.closed {
    left: 84px;
    width: 12em;
}

.ListEnterpricesBox.expanded {
    left: 221px;
}
.ListEnterprices{
    display: flex;
    flex-direction: column;
   text-overflow: ellipsis;
   align-items: start;
   justify-content: start;

    width: 100%;
    
}
.ListEnterpricesButton {
    display: flex;
    justify-content: flex-start !important;
    width: 100% !important
    
}

.ListEnterpricesButton p {
   text-align:start !important;

   text-overflow: ellipsis;
   color: var(--gray-600, #475467);
font-family: Inter;
font-size: 14px;
font-style: normal;
font-weight: 400;
line-height: 12.5px; /* 89.286% */
letter-spacing: 0.28px;
   
}

.imagenNavEnterPrice{
    width: 1.5em;
    height: 1.5em;
}

  