.progresBarAmplify{
  width: 100%;
  padding: 12px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.progressBar{
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  width: 100%;
}
.progressBar::before{
  content: "";
  position: absolute;
  background: var(--gray-300, #D0D5DD);

  border-radius: 5px;
  height: 2px;
  width: 100%;
  z-index: -1;
  transition: 0.4s ease;
  
}
.progress{
  position: absolute;
  background: var(--gray-300, #101828);

  border-radius: 5px;
  height: 2px;
  width: 50%;
  z-index: -1;
  transition: 0.4s ease;
}
.circleBar {
  background-color: #D0D5DD;
  color: #D0D5DD;
  border-radius: 50%;
  height: 8px;
  width: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #D0D5DD;
  transition: 0.4s ease;
}
.circleBar.activeBar {
  background-color: #FFF;
  color: #FFF;
  border: 2px solid #101828;
}
.circleBar.lastActiveBar{
  background-color: #101828;
  color: #101828;
  border: 2px solid #101828;
}
.progressContainerText {
  display: flex;
  justify-content: space-between;
  position: relative;
  padding: 8px 0;
  width: 108%;
}
.progressContainerText p {
  color: var(--sistem-texto-negro-ttulo, #0C0C0F);
  font-family:"Inter";
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.24px;
}
.progressContainerText p.activeBar {
  color: var(--sistem-texto-negro-ttulo, #0C0C0F);
  font-family:"Inter";
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.24px;
}