.tooltip-containerBP{
  position: relative;
  display: inline-block!important;
}
.tooltip-container{
  position: relative;
  display: flex;
}
.tooltip,
.tooltip-menu-secundario .tooltip  {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: -16px;
  top: calc(-100% + 14px);
  transform: translateX(100%);
  white-space: nowrap;
  padding: 0px 16px;
  border-radius: 6px;
  height: 44px;
  z-index: 5;
  box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.10);
  background: var(--primario-400, #EFF8FF);
  color: var(--gray-600, #475467);
  font-family: 'Inter';
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: -0.14px;
}

@media (max-width:1439px) {
  .tooltip,
  .tooltip-menu-secundario .tooltip {
    top: calc(-100% + 12px);
    right: -12px;
    height: 34px;

    color: var(--gray-600, #475467);
    font-family: 'Inter';
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: -0.12px;
  }
}