*{
    margin: 0;
    padding: 0;
}

.ChartsGraphics {
        flex-direction: column;
        align-items: flex-start;
        padding: 32px;
        gap: 32px;
        width: 100%;
        height: auto;
        background: var(--gray-white);
        border-radius: 8px;
        background-color: white;

}




.barsBenchamarkGenero {
   
    display: flex;
    width: 100%;
    justify-content: center;

}
.ChartsGraphicsContainer{
    width: 100%;
height: auto;
display: flex;
gap: 32px;
flex-direction: column;
align-items: flex-start;

}
.ChartsGraphicsTitle{
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 24px;
    
    display: flex;
    align-items: center;
    text-transform: uppercase;
    
    color: var(--texts-black);
    
    
}
.ChartsGraphicsText{
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
width: 100%;
height: auto;    
    color: var(--texts-black); 
}