.popup-content {
  display: flex;
  width: auto;
  height: 100%;
  padding: 16px;
  flex-direction: column;
  align-items: flex-end;
  gap: 8px;
  border-radius: 10px;
  background: var(--blanco-fondo, #FEFEFE);
  box-shadow: 8px 8px 12px 0px rgba(168, 168, 168, 0.10);
}

.popup-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1 0 0;
  color: var(--negro-texto, #4B4B5A);
  font-size: 12px;
  font-family:"Inter";
  font-weight: 300;
  line-height: 19.5px;
  letter-spacing: 0.26px;
  width: 208px;
  height: 78px;
}

.popup-close {
  font-size: 16px;
  font-weight: bold;
  border: none;
  background-color: transparent;
  cursor: pointer;
  width: 18px;
  height: 18px;
  display: flex;
  align-items: center;
}
