*{
  margin: 0px;
  padding: 0px;
}
.SelectedCheckBox{
/*   position: absolute; */
  left: 200px;
  width: 100%;
}
.selectCheckBox__control--is-focused {
  border-radius: 10px;
  border: 2px solid var(--primary-400, #F5A973)!important;
  box-shadow: 2px solid var(--primary-400, #F5A973)!important;
}
.selectCheckBox__control{
  display: flex;
  height: auto;
  align-items: center;
  gap: 24px;
  align-content: center;
  border-radius: 10px!important;
  background: var(--gray-25, #FCFCFD);
  border: 1.5px solid var(--gray-300, #D0D5DD)!important;
  padding: 0px 8px;
  width: 458px;
}
.css-4qk116-option{
  display: flex;
  padding: 6px!important;
  align-items: center;
  gap: 10px;
  flex: 1 0 0;
  align-self: center;
  max-width: 100%;
  overflow-x: hidden;
}
.selectCheckBox__option{
  padding: 6px!important; 
  display: flex;
  align-items: center;
  gap: 8px;
  align-self: stretch;
  background-color: #fff!important;
  /* Texto options */
  color: var(--gray-600, #475467)!important;
  font-family: 'Inter'!important;
  font-size: 12px!important;
  font-style: normal!important;
  font-weight: 300!important;
  line-height: 19.5px; 
  letter-spacing: 0.28px!important;
}
.selectCheckBox__placeholder{
  color: var(--gray-400, #98A2B3)!important;
  font-family: 'Inter'!important;
  font-size: 12px!important;
  font-style: normal!important;
  font-weight: 400!important;
  line-height: normal!important;
  letter-spacing: 0.28px!important;
}
.css-wsp0cs-MultiValueGeneric,.css-12a83d4-MultiValueRemove{
  display: flex;
  padding: 4px 6px;
  align-items: center;
  gap: 4px;
  border-radius: 4px!important;
  border: 0.5px 0.5px 0.5px 0px solid var(--gray-inactivo, #D9D9D9);
  background: var(--gray-100, #F2F4F7);
}
.selectCheckBox__multi-value__label,
.selectCheckBox__multi-value .css-1p3m7a8-multiValue{
  color: var(--sistem-texto-negro-prrafo, #34343F);
  font-family:"Inter"!important;
  font-size: 10px!important;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.2px;
}
.selectCheckBox__value-container--has-value,
.selectCheckBox__value-container,
.selectCheckBox__value-container--is-multi{
  flex-wrap: nowrap!important;
}
.css-12a83d4-MultiValueRemove svg{
  display: none;
}
.css-12a83d4-MultiValueRemove {
  background-image: url(../../Amplify/AmplifyManual/img/delete.svg);
  background-repeat: no-repeat;
  padding: 4px 6px;
  background-position: center;
  gap: 4px;
  width: 15px;
  border-radius: 0px 4px 4px 0px!important;
}

.css-1p3m7a8-multiValue{
  border-radius: 4px;
  border: 0.5px solid var(--gray-inactivo, #D9D9D9);
