.selectCheckBox__menu-list {
  display: flex;
  width: 100%;
  padding: 8px!important;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  border-radius: 4px;
  background: var(--blanco-base, #FFF);
  max-height: 300px;
  overflow-y: auto; /* Cambiado a 'auto' para desplazamiento vertical automtico */
  overflow-x: hidden;
}

.selectCheckBox__menu {
  top: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  padding: 8px;
  gap: 8px;
  border-radius: 6px!important;
  background: var(--blanco-base, #FFF)!important;
  box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.10)!important;
  margin: 0px!important;
  overflow-y: auto; 
  overflow-x: hidden; 
  scroll-behavior: smooth;
}
.css-4qk116-option:hover{
  border-radius: 4px;
  background: var(--primario-50, #FFFBF4);
}
