.login__formCrearCuenta{
display: flex;
width: auto;
flex-direction: column;
align-items: center;
gap: 60px;
flex-shrink: 0;
}
.form__titles{
    display: flex;
flex-direction: column;
align-items: flex-start;
padding: 0px;
gap: 32px;

width: 425px;
height: auto;
}
.titles{

    display: flex;
flex-direction: column;
justify-content: center;
flex: 1 0 0;
color: var(--primario-60, #FF8C00);
text-align: center;
font-family:"Inter";
font-size: 24px;
font-style: normal;
font-weight: 600;
line-height: 24px;
}
.form__titlesContainer_text{
    display: flex;
flex-direction: column;
align-items: center;
padding: 0px;
gap: 12px;
width: 100%;
height: auto;

}


.form__inputs{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 23px;
    width: auto;
    height: 146px;
}
.form__label--text{
   width: auto;
height: auto;

font-family: 'Inter', sans-serif;
font-style: normal;
}
.form__titlesContainer_text p{
    color: var(--negro-texto, #4B4B5A);
text-align: center;
font-family:"Inter";
font-size: 14px;
font-style: normal;
font-weight: 400;
line-height: 24px;
    display: flex;
width: 374px;
height: 45px;
align-items: flex-start;
gap: 10px;
flex-shrink: 0;
}

.form__inputs{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 23px;
    width: auto;
    height: 146px;
}
.form__label--text{
    color: var(--negro-ttulos, #0C0C0F);
    font-family:"Inter";
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    display: flex;
flex-direction: column;
justify-content: center;
flex: 1 0 0;

    
}

.form__titles--text{
    width: 406.58px;
height: 16px;
font-family: 'Inter', sans-serif;
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 16px;
color: rgba(30, 30, 28, 0.25);
}
.login__form--button{
   width: 406px;
}
.form__input--texts {
    display: flex;
height: auto;
padding: 10px 6px;
flex-direction: column;
justify-content: center;
align-items: flex-start;
align-self: stretch;
border-radius: 8px;
border: 2px solid var(--gris-gris-borde, #E2E7F1);
background: var(--blanco, #FFF);
}
.form__input--texts::placeholder{
    font-family: 'Inter', sans-serif;
    font-style: italic;
    font-weight: 400;
    font-size: 12px;
    line-height: 24px;
    display: flex;
    align-items: center;
    color: var(--texts-gray);
    


}
.inputs__cont{
    display: flex;
    justify-content: center;
}

.cont__login--link{
    width: 100px;
    height: 30px;
text-decoration: none;    
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    display: flex;
    align-items: center;
    letter-spacing: 0.005em;
    
    color: var(--blue);
    
}

#change__screen{
    display: none;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 16px;
    
    width: 406.58px;
    height: 272px;
}


.disabled {
    display: flex;
    width: 146px;
    padding: 8px 12px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 8px;
background: var(--primary-600, #EF7B28);
box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.25);
color: var(--blanco-fondo, #FEFEFE);
font-family:"Inter";
font-size: 14px;
font-style: normal;
font-weight: 500;
line-height: 21px;
letter-spacing: 0.28px;
box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.25);
}

.disabled:hover {border-radius: 8px;
    background: var(--primary-500, #F18F49);
    
    /* hover */
    box-shadow: 0px 2px 4px 0px #D0D5DD;}
#change__screen--final{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 23px;
    align-self: stretch;


}
.container_form_input{
   display: flex;
flex-direction: column;
align-items: flex-start;
gap: 8px;
align-self: stretch;
}
.login__final--form{
    display: flex;
    width: 146px;
    padding: 8px 12px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
background: #FFEFE0;
color: #989898;
font-family:"Inter";
font-size: 14px;
font-style: normal;
font-weight: 400;
line-height: 21px;
letter-spacing: 0.28px;

}
.form__inputs--image--containerIniciarSesion {
    display: flex;
    flex-direction: row;
    position: absolute;
    padding: 0px;
    margin-left: 390px;
    width: 24px;
    margin-top: 31px;
    height: 24px;
}
#name-error,
#password-error,
#email-error {
    color: var(--estados-danger, #FF696A);
    font-family:"Inter";
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.24px;
}
